











































.tag-wrapper {
    min-height: calc(100vh - 63px);
    padding-top: 50px;
    padding-bottom: 30px;

    .title {
        text-align: center;

        span {
            margin-left: 10px;
            color: #999;
            cursor: pointer;
            font-size: 12px;
        }
    }

    .tag-list {
        margin-top: 30px;

        li {
            display: inline-block;
            border-bottom: 1px solid #999;
            word-wrap: break-word;
            margin: 10px;
            cursor: pointer;
        }
    }
}
